@import url(https://fonts.googleapis.com/css2?family=Dosis&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.6em;
  height: 90px;
  margin-bottom: 0px;
}

@media (min-width: 828px) {
  #header-main {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    border-bottom: 1px black solid;
    width: 80%;
    font-family: 'Dosis', sans-serif;
    font-weight: bold;
  }
}

img {
  height: 29px;
  width: auto;
}

#cart-details {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 50px;
  margin: 0 25px 0 60px;
  color: black;
}

ul {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 400px;
}

li {
  color: black;
  text-decoration: none;
}

li:hover {
  text-decoration: underline;
}

#icon {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 35% 200px 0 50px;
  font-size: 60px;
  color: white;
  text-decoration: none;
  border: 1px black solid;
  height: 4em;
  width: 4em;
  background-color: black;
  border-radius: 50%;
}

main {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;
  font-family: 'Lato', sans-serif;
}

#desc-box {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.6em;
  width: 20%;
  height: 10%;
  position: absolute;
  z-index: 1;
  margin-right: 55%;
  border: 2px black solid;
  color: black;
}

#desc-box:hover {
  background-color: black;
  color: white;
  transition: ease 0.3s;
}

#home-photo {
  height: 670px;
  width: auto;
  position: relative;
  z-index: 0;
}

@media (min-width: 1450px) {
  #home-photo {
    height: 61em;
    width: auto;
  }
  #desc-box {
    margin-right: 60%;
  }
}
@media (min-width: 2560px) {
  #home-photo {
    height: 84em;
    width: auto;
  }
}
@media (min-width: 3840px) {
  #home-photo {
    height: 129em;
    width: auto;
  }
  #desc-box {
    font-size: 3em;
    margin-left: 2%;
    width: 20%;
  }
}

#about-box p {
  text-align: center;
}

#about-box {
  position: relative;
  width: 700px;
  right: 15%;
  margin-top: 5%;
  font-family: 'Lato', sans-serif;
  font-size: 1.4em;
}

#icon-box {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-top: 6%;
  width: 100%;
}

#icon-box img {
  height: 70px;
  width: auto;
}

@media (min-width: 1450px) {
  #about-box {
    position: relative;
    width: 1000px;
    right: 15%;
    margin-top: 7%;
    font-size: 1.5em;
  }
  #icon-box img {
    height: 75px;
    width: auto;
  }
}
@media (min-width: 2560px) {
  #about-box {
    position: relative;
    width: 1500px;
    right: 15%;
    margin-top: 7%;
    font-size: 2.2em;
  }
  #icon-box img {
    height: 88px;
    width: auto;
  }
}
@media (min-width: 3840px) {
  #about-box {
    position: relative;
    width: 2000px;
    right: 15%;
    margin-top: 7%;
    font-size: 3.5em;
  }
  #icon-box img {
    height: 100px;
    width: auto;
  }
}

.item-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5);
  width: 300px;
  height: 400px;
  margin: 10px;
  font-size: 20px;
  font-family: 'Lato', sans-serif;
}

.item-card img {
  height: 200px;
  border-bottom: 1px black solid;
  padding-bottom: 16px;
  width: auto;
}

#catalogue {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  width: 70%;
  float: right;
}

#add-button {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px black solid;
  margin-top: 15px;
  font-size: 12px;
  height: 25px;
  width: 100px;
}

#add-button:hover {
  color: white;
  background-color: black;
  transition: ease 0.3s;
}

@media (min-width: 2560px) {
  .item-card {
    width: 400px;
    height: 500px;
    margin: 20px;
    font-size: 25px;
    font-family: 'Lato', sans-serif;
  }
  .item-card img {
    height: 150px;
    border-bottom: 2px black solid;
    padding-bottom: 50px;
    width: auto;
  }
  #add-button {
    margin-top: 3a0px;
    font-size: 17px;
    height: 40px;
    width: 160px;
  }
}
@media (min-width: 3840px) {
  .item-card {
    width: 800px;
    height: 900px;
    margin: 20px;
    font-size: 50px;
  }
  .item-card img {
    height: 400px;
    border-bottom: 2px black solid;
    padding-bottom: 50px;
    width: auto;
  }
  #add-button {
    margin-top: 50px;
    font-size: 20px;
    height: 50px;
    width: 200px;
  }
}

#shopping-cart {
  width: 930px;
  float: right;
  font-family: 'Lato', sans-serif;
}

#cart-button-box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.cart-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px black solid;
  color: black;
  width: 220px;
  height: 50px;
  margin: 10px;
}

.cart-buttons:hover {
  background-color: black;
  color: white;
  transition: ease 0.3s;
}

.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100px;
  border-bottom: 1px black dashed;
  font-size: 20px;
}

.cart-item img {
  height: 90px;
  width: 110px;
  margin-right: 20px;
}

#price-div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 300px;
  margin-right: 40px;
  font-size: 15px;
}

#info-div {
  display: flex;
  justify-content: center;
  align-items: center;
}

#remove-div {
  border: 1px black solid;
  padding: 5px;
}

#remove-div:hover {
  background: black;
  color: white;
  transition: ease 0.3s;
}

@media (min-width: 1450px) {
  #shopping-cart {
    width: 1410px;
  }
  .cart-buttons {
    font-size: 20px;
    width: 340px;
    height: 80px;
    margin: 10px;
  }
  .cart-item {
    height: 150px;
    font-size: 30px;
  }
  #price-div {
    font-size: 20px;
  }
  #remove-div {
    padding: 5px;
    font-size: 20px;
  }
  .cart-item img {
    height: 100px;
    width: 120px;
  }
}
@media (min-width: 2560px) {
  #shopping-cart {
    width: 2030px;
  }
  .cart-buttons {
    font-size: 25px;
    width: 390px;
    height: 100px;
    margin: 10px;
  }
  .cart-item {
    height: 180px;
    font-size: 35px;
  }
  #price-div {
    font-size: 25px;
  }
  #remove-div {
    padding: 5px;
    font-size: 25px;
  }
  .cart-item img {
    height: 110px;
    width: 140px;
  }
}
@media (min-width: 3840px) {
  #shopping-cart {
    width: 3050px;
  }
  .cart-buttons {
    font-size: 25px;
    width: 470px;
    height: 150px;
    margin: 20px;
  }
  .cart-item {
    height: 300px;
    font-size: 46px;
  }
  #price-div {
    font-size: 32px;
  }
  #remove-div {
    padding: 5px;
    font-size: 32px;
  }
  .cart-item img {
    height: 150px;
    width: 190px;
  }
}

